@charset "UTF-8";
:root {
  --astorRender: url("../img/pictures/d-a.png");
  --width: calc(0px + (260 - 0) * ((100vw - 320px) / (1024 - 320)));
  --font-color-default: rgb(9, 9, 27);
  --font-color-secondary: rgb(80, 60, 42);
  --bgInverse2: #494949;
  --accent: #897666;
  --background-color: rgb(210, 242, 255);
  --background-second: rgb(2, 10, 16);
  --background-selected: rgba(43, 45, 65, 0.337);
  --background-transparent: rgba(14, 14, 19, 0.855);
  --selected-color: rgb(94, 77, 168);
  --themes-color: rgb(167, 167, 167);
  --background-thumb: rgba(86, 56, 20, 0.767);
  --scrollbar-thumb-bgcolor: rgb(231, 231, 231);
  --border-color: rgba(86, 56, 20, 0.623);
  --pos: center center;
  --size: 0;
  --inset-top: 0;
  --inset-right: 100%;
  --inset-bottom: 100%;
  --inset-left: 0;
  --stripe-width: 10px;
  --nav-marker-border: rgb(59, 134, 134);
  --sidebar-width: 28.5rem;
  --duration: 0.3s;
  --button-size: 4rem;
  --mob-expanded-width: 320px;
  --mob-collapsed-width: 4rem;
}

:root {
  --swiper-scrollbar-top: auto;
  --swiper-scrollbar-bg-color: rgba(0, 0, 0, 0.1);
  --swiper-scrollbar-drag-bg-color: rgba(0, 0, 0, 0.5);
  --swiper-scrollbar-size: 0.4rem;
  --swiper-scrollbar-bottom: 0.4rem;
  --swiper-scrollbar-sides-offset: 1%;
  --swiper-scrollbar-border-radius: 1rem;
  --swiper-theme-color: $themeColor;
  --swiper-navigation-size: 3rem;
  --swiper-pagination-color: #f65e38;
  --swiper-pagination-bullet-opacity: 1;
  --swiper-pagination-bullet-size: 1.2rem;
  --swiper-pagination-bullet-border-radius: 50%;
  --swiper-pagination-bullet-inactive-opacity: 0.4;
  --swiper-pagination-bullet-inactive-color: rgb(0, 0, 0);
  --swiper-pagination-bullet-width: 1.2rem;
  --swiper-pagination-bullet-height: 1.2rem;
  --swiper-pagination-top: auto;
  --swiper-pagination-bottom: 0.5rem;
  --swiper-pagination-bullet-horizontal-gap: 0.5rem;
  --swiper-transition-opacity: 0.9s;
}

.bg-grad {
  pointer-events: none;
  position: absolute;
  z-index: 0;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-position: left;
  background: linear-gradient(to right, transparent 50%, rgb(230, 217, 205), rgb(230, 217, 205));
}

.hidden {
  overflow: hidden;
}

.bg-top,
.bg-bottom {
  pointer-events: none;
  position: absolute;
  left: 0;
  width: 100%;
  height: 45%;
  z-index: 1;
  display: inline-block;
  text-align: right;
}

.bg-top {
  top: 0;
  background-image: linear-gradient(rgb(255, 255, 255), transparent 70%);
}

.bg-card, .section-website .slide__slide-column {
  background: rgba(247, 244, 241, 0.431372549);
  backdrop-filter: blur(8px);
}

.bg-bottom {
  bottom: 0;
  transform: translateY(20px);
  background-image: linear-gradient(transparent, rgb(255, 255, 255) 40%);
}

.bg-image, .slide-temples__img-content {
  position: absolute;
  z-index: 0;
  transform: translateY(10px);
  left: 0;
  bottom: 0;
  width: 100%;
  height: 30%;
  background-image: linear-gradient(transparent, rgba(54, 54, 54, 0.7725490196) 70%);
}

.background-clip::before {
  content: "Храмы";
  display: block;
  position: absolute;
  background-clip: text;
  background: linear-gradient(-135deg, transparent 40%, rgb(9, 9, 27));
  background-clip: text;
  color: transparent;
  text-shadow: none;
  background-size: 300% 100%;
  background-position: 100% 0%;
  transition: background-position 0.5s;
  mix-blend-mode: multiply;
}

.frame-container {
  position: absolute;
  margin: 0 auto;
  z-index: 0;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border: 40px solid transparent;
  border-image-repeat: repeat;
}
@media screen and (max-width: 320px) {
  .frame-container {
    border: 20px solid transparent;
    border-image-repeat: repeat;
  }
}

.box-shadow-card, .slide__slide-column {
  box-shadow: 0 0 6px 1px #e6d8cc;
}

.box-shadow {
  box-shadow: 0 1px 7px #a08973;
  backdrop-filter: blur(10px);
}

@media (min-width: 490.98px) and (max-width: 1920px) {
  .font-size-calc-h2 {
    font-size: calc(28px + 22 * (100vw - 490px) / 1430);
  }
}

.top-box {
  min-height: 100vh;
  padding: 8rem 0 0;
}
@media screen and (max-width: 1440px) {
  .top-box {
    padding: 18rem 0 14rem;
    min-height: 100%;
  }
}
@media (max-width: 768px) {
  .top-box {
    padding: 18rem 0 4rem;
  }
}

.hr {
  position: relative;
  z-index: 9;
  opacity: 0.7;
  display: flex;
  justify-content: center;
}
.hr img {
  max-width: 255px;
  object-fit: contain;
}

.gradient-border-bottom {
  background-image: linear-gradient(to left top, rgba(205, 215, 238, 0.61) 25%, transparent 75%);
}

.gradient-services {
  background-image: linear-gradient(to right, #050202 20%, #1c153f 50%, #050202 80%);
}

.icon-style {
  display: flex;
  align-items: center;
  height: 100%;
  padding: 0 1.5rem;
  border: 1px var(--border-color);
  border-style: none solid;
}

.text-shadow-black {
  text-shadow: 0 0 9px rgba(0, 0, 0, 0.4745098039);
}

.text-shadow, .slide-temples__info {
  text-shadow: 0 0 15px rgba(0, 0, 0, 0.4392156863), 0 0 1em rgba(0, 0, 0, 0.4392156863), 0 0 0.2em blue;
}

.link_split {
  position: relative;
  outline: 0;
  line-height: 1;
  transition: color 0s 0.3s;
  display: inline-block;
  white-space: nowrap;
}
.link_split:hover {
  text-shadow: none;
  color: transparent;
  transition: none;
}
.link_split:hover::before {
  transform: translate3d(0px, -2px, 0);
}
.link_split:hover::after {
  transform: translate3d(0px, 2px, 0);
}
.link_split:hover::before, .link_split:hover::after {
  transition: color 0.3s, transform 0.3s;
}
.link_split::before, .link_split::after {
  content: attr(data-letters);
  opacity: 1;
  position: absolute;
  top: 0;
  left: 0;
  transition: color 0.3s, opacity 0.3s, transform 0.5s;
}
.link_split::before {
  clip-path: polygon(0 0, 100% 0%, 100% 30%, 0 65%);
}
.link_split::after {
  clip-path: polygon(0 65%, 100% 30%, 100% 100%, 0 100%);
}

.link_underline {
  position: relative;
  transition: color 0.3s ease-in-out;
}
.link_underline::before {
  content: "";
  transform: scaleX(0);
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 1px;
  transition: transform 0.3s ease-in-out;
  opacity: 0.6;
  background-color: rgb(80, 60, 42);
}
.link_underline::after {
  bottom: 0;
  left: 0;
}
.link_underline:hover::before {
  transform: scaleX(1);
}
.link_underline:hover a {
  opacity: 1;
}

.link-hover {
  color: #503c2a;
}

.is-open {
  display: grid;
  width: 100%;
  transition: grid-template-rows 0.4s ease-in-out;
  grid-template-columns: 1fr;
  grid-template-rows: 0fr;
}
.is-open._is-open {
  grid-template-rows: 1fr;
}

.transform-perspective {
  transform-style: preserve-3d;
  backface-visibility: hidden;
  perspective: 100px;
}
.transform-perspective .transform-box {
  transition: all 0.8s cubic-bezier(0.77, 0, 0.175, 1);
}
.transform-perspective .transform-box:hover {
  opacity: 0.3 !important;
  transform-origin: 50% -100%;
  transform: rotateX(-16deg) translateZ(-22px);
}

.grad_underline {
  /* Определяем фоновые изображения */
  transition: background-size 400ms;
  /* Размер фона для каждого слоя */
  background-image: linear-gradient(to left, transparent, transparent), linear-gradient(to left, var(--background-selected), var(--font-color-active));
  /* Позиционирование фона */
  background-repeat: no-repeat;
  /* Повторение фона */
  background-position: 100% 100%, 0 100%; /* Второе изображение */
  /* Переход для изменения размера фона */
  background-size: 100% 1px, 0 1px; /* Второе изображение */
}

.gradient-button {
  background-image: linear-gradient(to right, #9a7a5f 0%, #b29f94 51%, #9a7a5f 100%);
  text-align: center;
  text-transform: uppercase;
  transition: 0.5s;
  background-size: 200% auto;
  box-shadow: 0 0 2px #2b2b2b;
  display: block;
}
.gradient-button:hover {
  background-position: right 5% center;
  /* change the direction of the change here */
}

.gradient-header {
  background-image: linear-gradient(-40deg, #9a7a5f 20%, rgba(154, 122, 95, 0.431372549) 45%, #9a7a5f 75%);
}

.gradient-footer {
  background: linear-gradient(to right, rgb(230, 217, 205), transparent 50%, rgb(230, 217, 205));
}

.pagination {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  margin-top: 6rem;
}
.pagination .swiper-pagination-bullet {
  width: 15px;
  height: 15px;
  content: "";
  border-radius: 50%;
  background: #2a3514;
  transition: background 0.4s ease-in-out;
}
.pagination .swiper-pagination-bullet:not(:last-child) {
  margin-right: 2rem;
}
.pagination .swiper-pagination-bullet-active {
  width: 15px;
  height: 15px;
  content: "";
  border-radius: 50%;
  background: #9acd32;
  scale: 1.2;
  transition: scale 0.5s ease-in-out;
}
.pagination.swiper-pagination-lock {
  display: none;
}

.render-mask {
  position: relative;
  z-index: 1;
  mask-image: var(--astorRender);
  mask-size: auto 100%;
  mask-repeat: no-repeat;
  mask-position: top left;
  -webkit-mask-size: auto 100%;
  -webkit-mask-repeat: no-repeat;
  -webkit-mask-position: top left;
  position: absolute;
  height: 1000px;
  width: 700px;
  top: -100px;
  left: -75px;
}
.render-mask::before {
  content: "";
  display: block;
  position: absolute;
  top: 0px;
  left: 0px;
  right: 0px;
  bottom: 0px;
  height: 100%;
  width: 100%;
  background: linear-gradient(to bottom, var(--bgInverse2) 400px, var(--accent));
  background-size: 150% 150%;
  background-position: top center;
}

.mask-render {
  position: fixed;
  background: url("../img/pictures/d-a.png") top left/contain no-repeat;
  mask-size: 100% 100vh;
  mask-repeat: no-repeat;
  height: 100vh;
  width: 100%;
  top: 190px;
  left: 0;
  z-index: 1;
  pointer-events: none;
  opacity: 0.3;
  object-fit: contain;
}
@media screen and (max-width: 320px) {
  .mask-render {
    width: 320px;
  }
}

@media (min-width: 920.99px) {
  /* FLOW: RIGHT */
  [data-tooltip] {
    position: relative;
  }
  [data-tooltip]::after {
    content: attr(data-tooltip);
    border-radius: 0.6rem;
    border: 1px solid var(--border-color);
    background-color: rgba(35, 52, 59, 0.8705882353);
    position: absolute;
    padding: 1.2rem;
    color: #fff;
    opacity: 0;
    visibility: hidden;
    transition: all 0.4s ease-in-out;
    font-family: "Sofia Sans Extra Condensed", sans-serif;
    font-weight: 200;
    font-size: 1.3rem;
    letter-spacing: 0.2em;
    text-transform: uppercase;
    z-index: 9;
    white-space: nowrap;
  }
  [data-tooltip]::before {
    content: "";
    border: 9px solid transparent;
    border-right: 9px solid var(--font-color-active);
    position: absolute;
    z-index: 9;
    margin-left: -20px;
    bottom: 12%;
    opacity: 0;
    visibility: hidden;
    transition: all 0.4s ease-in-out;
  }
  [data-tooltip]:hover::before,
  [data-tooltip]:hover::after {
    visibility: visible;
    opacity: 1;
    display: block;
  }
  [data-tooltip=""]::before,
  [data-tooltip=""]::after {
    display: none !important;
  }
  [data-tooltip][flow^=right]::after {
    top: 50%;
    left: calc(120rem + 10px);
    transform: translate(0.5em, -50%);
  }
  [data-tooltip][flow^=right]::before {
    top: 50%;
    left: 121rem;
    transform: translate(0.5em, -50%);
  }
  [data-tooltip][flow^=right]:hover::before,
  [data-tooltip][flow^=right]:hover::after {
    animation: data-tooltip 300ms ease-out forwards;
  }
  @keyframes data-tooltip {
    to {
      opacity: 1;
      transform: translate(-110rem, -50%);
    }
  }
}
.breadcrumb a:after {
  content: "";
  border-top: 12px solid transparent;
  border-bottom: 12px solid transparent;
  border-left: 12px solid #e7e9eb;
  position: absolute;
  right: -12px;
  top: 0;
  z-index: 1;
}

html {
  font-style: normal;
  font-size: 62.5%;
  font-weight: 400;
  font-family: "Sofia Sans Extra Condensed", sans-serif;
  height: 100%;
  counter-reset: heading;
  letter-spacing: 1.1px;
  color: rgb(9, 9, 27);
}
@media screen and (max-width: 1024px) {
  html {
    font-size: 50%;
  }
}

body {
  scrollbar-width: none;
  overscroll-behavior: none;
  font: 2rem/2 "Sofia Sans Extra Condensed", sans-serif;
  background: rgb(255, 255, 255);
  background-color: #fff;
}
body.no-scroll {
  overflow: hidden;
}
@media screen and (max-width: 490px) {
  body {
    font: 1.8rem/1.8 "Sofia Sans Extra Condensed", sans-serif;
  }
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  border: none;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

img,
svg {
  vertical-align: top;
  user-select: none;
  pointer-events: none;
  width: 100%;
  height: auto;
  max-width: none;
}

a {
  cursor: pointer;
  text-decoration: none;
  color: inherit;
}

a:hover {
  text-decoration: none;
  vertical-align: baseline;
  background-color: transparent;
}

a:focus {
  text-decoration: none;
}

a:visited {
  vertical-align: middle;
  text-emphasis-style: none;
}

h2,
h3 {
  font-family: "Ponomar", sans-serif;
}

input,
button,
textarea {
  border: none;
}

button,
input[type=submit] {
  display: inline-block;
  cursor: pointer;
  background: transparent none;
  box-shadow: none;
}

input::-ms-clear {
  display: none;
}

input,
textarea,
button,
select {
  font-family: inherit;
  font-size: inherit;
  color: inherit;
  background-color: transparent;
}

input:focus,
input:active,
button:focus,
button:active {
  outline: none;
}

input,
textarea,
button,
select {
  font-family: inherit;
  font-size: inherit;
  color: inherit;
  background-color: transparent;
}

button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

label {
  cursor: pointer;
}

a:focus,
a:active,
input:focus,
textarea:focus {
  outline: none;
}

ul,
ol,
li {
  list-style: none;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: inherit;
  font-style: inherit;
}

::selection {
  color: rgb(155, 153, 153);
  background-color: rgba(0, 84, 99, 0.247);
  text-shadow: 0 1px 0 #000;
}

::-moz-selection {
  color: rgb(155, 153, 153);
  background-color: rgba(0, 84, 99, 0.247);
  text-shadow: 0 1px 0 #000;
}

::-webkit-scrollbar {
  width: 0.5rem;
  padding: 0.031em;
  transition: all 0.4s ease-in-out;
}

::-webkit-scrollbar-track {
  background: var(--scrollbar-thumb-bgcolor);
  opacity: 0;
}

::-webkit-scrollbar-thumb {
  cursor: pointer;
  border: 1px solid var(--border-color);
  border-radius: 2px;
  background-color: var(--themes-color);
  background-image: -webkit-linear-gradient(45deg, var(--background-thumb) 25%, transparent 25%, transparent 50%, var(--background-thumb) 50%, var(--background-thumb) 75%, transparent 75%, transparent);
}

::-webkit-scrollbar-thumb:hover {
  opacity: 1;
  transition: background-image 0.4s ease-in-out;
  background-color: var(--background-thumb);
  background-image: -webkit-linear-gradient(45deg, var(--themes-color) 25%, transparent 25%, transparent 50%, var(--themes-color) 50%, var(--themes-color) 75%, transparent 75%, transparent);
}

@font-face {
  font-family: "icon-set";
  font-display: swap;
  src: url("../fonts/icon-set.eot") format("eot"), url("../fonts/icon-set.woff") format("woff"), url("../fonts/icon-set.woff2") format("woff2"), url("../fonts/icon-set.ttf") format("truetype");
  font-weight: 400;
  font-style: normal;
}
.perform {
  position: relative;
  display: grid;
}
.perform > * {
  grid-area: 1/-1;
}
.perform__bg-clip {
  position: relative;
  z-index: 0;
  display: flex;
  width: 100%;
  height: 100vh;
}
@media screen and (max-width: 1440px) {
  .perform__bg-clip {
    height: 100%;
  }
}
.perform__body {
  grid-row: 1/-1;
  position: relative;
  z-index: 9;
  display: flex;
  align-items: center;
}
.perform__container {
  width: 100%;
}
.perform__content {
  position: relative;
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: end;
  gap: 6rem;
}
.perform__row {
  width: 100%;
  display: grid;
  grid-template-columns: max-content minmax(400px, 800px);
  column-gap: 2rem;
  justify-content: space-between;
}
@media screen and (max-width: 1140px) {
  .perform__row {
    grid-template-columns: minmax(288px, 1fr);
  }
}
.perform__column {
  width: 100%;
  display: flex;
  position: relative;
  font-family: "Ponomar", sans-serif;
  line-height: 0.7;
}
.perform__column:nth-child(1) {
  margin-bottom: 7rem;
  z-index: 9;
  padding-right: 3rem;
  align-self: end;
  grid-column: 1/2;
  grid-row: 1/3;
}
@media screen and (max-width: 1140px) {
  .perform__column:nth-child(1) {
    padding-right: 0;
    grid-column: 1/2;
    grid-row: 1/2;
    margin-bottom: 2rem;
  }
}
.perform__column:nth-child(2) {
  grid-column: 2/3;
  grid-row: 1/2;
  padding-bottom: 4rem;
  justify-content: flex-end;
  padding-right: 3rem;
}
@media screen and (max-width: 1140px) {
  .perform__column:nth-child(2) {
    padding-bottom: 2rem;
    grid-column: 1/2;
    grid-row: 2/3;
  }
}
.perform__column:nth-child(2) ._icon {
  transform: translateY(1rem);
}
.perform__column:nth-child(2) ._icon::before {
  font-size: 3.2rem;
}
.perform__column:nth-child(3) {
  max-width: 700px;
  flex-direction: column;
  grid-column: 2/3;
  grid-row: 2/3;
  justify-self: end;
}
@media screen and (max-width: 1140px) {
  .perform__column:nth-child(3) {
    grid-column: 1/2;
    grid-row: 3/4;
    align-items: end;
  }
}
@media (min-width: 490.99px) {
  .perform__column:nth-child(3) {
    padding-left: 56px;
  }
}
@media screen and (max-width: 490px) {
  .perform__column:nth-child(3) {
    max-width: 340px;
    justify-self: center;
  }
}
.perform__column:not(:first-child) {
  z-index: 0;
}
.perform__date {
  display: flex;
  align-items: center;
  gap: 1.6rem;
  margin-left: 1.6rem;
}
.perform__date .day {
  display: flex;
  align-items: end;
  letter-spacing: -2px;
  font: 96px "Ponomar", sans-serif;
  line-height: 1;
  transform: scaleY(1.2);
}
@media (min-width: 490.98px) and (max-width: 1920px) {
  .perform__date .day {
    font-size: calc(52px + 44 * (100vw - 490px) / 1430);
  }
}
@media screen and (max-width: 490px) {
  .perform__date .day {
    font-size: 52px;
  }
}
.perform__date .data-wrapper {
  display: flex;
  height: 100%;
  flex-direction: column;
  justify-content: space-evenly;
}
.perform__date .data-wrapper .status,
.perform__date .data-wrapper .month {
  font-family: "Ponomar", sans-serif;
  font-size: 3.4rem;
  line-height: 1;
}
.perform__date .data-wrapper .status {
  font-size: inherit;
}
.perform__slide-perform {
  width: 100%;
  position: relative;
}

.mask-render {
  background: url("../img/pictures/a.png") top left/contain no-repeat;
  mask-size: 80% 100vh;
  mask-repeat: no-repeat;
  height: 100vh;
  width: 50%;
  width: 987px;
  position: absolute;
  top: 0;
  z-index: 1;
  pointer-events: none;
  opacity: 0.5;
  object-fit: contain;
}
@media (min-width: 320.98px) and (max-width: 1920px) {
  .mask-render {
    width: calc(320px + 667 * (100vw - 320px) / 1600);
  }
}
@media screen and (max-width: 320px) {
  .mask-render {
    width: 320px;
  }
}

.bg-clip__item:nth-child(1) {
  flex: 0 1 70%;
  background: #e6d9cd;
  overflow: hidden;
